export default {
  model: {
    prop: 'visible',
    event: 'closeDialog'
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    position: {
      type: String,
      default: 'middle'
    },
    title: {
      type: String,
      default: ''
    },
    cancelColor: {
      type: String,
      default: '#6c6a75'
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    onCancel: {
      type: Function,
      default: () => {}
    },
    confirmText: {
      type: String,
      default: '确定'
    },
    bgTitle: {
      type: Boolean,
      default: false
    },
    bgSrc: {
      default: ''
    },
    one: {
      default: false
    },
    cancelAutoClose: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  },
  mounted () {

  },
  computed: {
  },
  methods: {
    cancle () {
      if (this.cancelAutoClose) {
        this.$emit('closeDialog', false)
        this.onCancel()
      } else {
        this.onCancel(() => {
          this.$emit('closeDialog', false)
        })
      }
    },
    confirm () {
      this.$emit('onConfirm')
    }
  }
}



